<template>
  <div class="settings-json-buffer-index">
    <h1>JSON 缓冲区</h1>

    <div class="toolbar">
      <el-button type="default" @click="reset"> Reset </el-button>
      <el-button type="default" @click="fetch"> Refresh </el-button>
    </div>

    <pre class="json-text-area">{{ viewObjectString }}</pre>

    <div class="toolbar">
      <el-button type="default" @click="copy"> Copy </el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "settings-json-buffer-index",

  computed: {
    vo() {
      return this.$store.getters["jsonbuffer/vo"];
    },

    viewObjectString() {
      return JSON.stringify(this.vo, null, 4);
    },
  },

  methods: {
    fetch() {
      return this.$store.dispatch("jsonbuffer/fetch");
    },

    reset() {
      return this.$store.dispatch("jsonbuffer/reset");
    },

    copy() {
      let text = this.viewObjectString;
      this.$store.dispatch("clipboard/copy", text);
    },
  },

  mounted() {
    this.fetch();
  },
};
</script>

<style scoped>
.settings-json-buffer-index {
  text-align: left;
}

.json-text-area {
  padding: 10px;
  background-color: rgb(184, 184, 184);
}
</style>
